.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-descriptions {
  box-sizing: border-box;
  font-size: 14px;
  color: #8394be; }
  .tu-descriptions__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px; }
    .tu-descriptions__title {
      font-size: 14px;
      font-weight: bold; }
  .tu-descriptions__body {
    color: #9baacf; }
    .tu-descriptions__body tr,
    .tu-descriptions__body th,
    .tu-descriptions__body td {
      border: none; }
    .tu-descriptions__body .tu-descriptions__table {
      border-collapse: collapse;
      width: 100%;
      table-layout: fixed; }
      .tu-descriptions__body .tu-descriptions__table .tu-descriptions-item__cell {
        box-sizing: border-box;
        text-align: left;
        font-weight: normal;
        line-height: 1.5; }
        .tu-descriptions__body .tu-descriptions__table .tu-descriptions-item__cell.is-left {
          text-align: left; }
        .tu-descriptions__body .tu-descriptions__table .tu-descriptions-item__cell.is-center {
          text-align: center; }
        .tu-descriptions__body .tu-descriptions__table .tu-descriptions-item__cell.is-right {
          text-align: right; }
  .tu-descriptions .is-bordered {
    table-layout: auto; }
    .tu-descriptions .is-bordered .tu-descriptions-item__cell {
      border: 1px solid rgba(190, 200, 228, 0.38);
      padding: 12px 10px; }
  .tu-descriptions :not(.is-bordered) .tu-descriptions-item__cell {
    padding-bottom: 12px; }
  .tu-descriptions--large.is-bordered .tu-descriptions-item__cell {
    padding: 16px 14px; }
  .tu-descriptions--large:not(.is-bordered) .tu-descriptions-item__cell {
    padding-bottom: 14px; }
  .tu-descriptions--small {
    font-size: 12px; }
    .tu-descriptions--small.is-bordered .tu-descriptions-item__cell {
      padding: 8px 10px; }
    .tu-descriptions--small:not(.is-bordered) .tu-descriptions-item__cell {
      padding-bottom: 8px; }
  .tu-descriptions--mini {
    font-size: 12px; }
    .tu-descriptions--mini.is-bordered .tu-descriptions-item__cell {
      padding: 6px 10px; }
    .tu-descriptions--mini:not(.is-bordered) .tu-descriptions-item__cell {
      padding-bottom: 6px; }

.tu-descriptions-item {
  vertical-align: top; }
  .tu-descriptions-item__container {
    display: flex; }
    .tu-descriptions-item__container .tu-descriptions-item__label,
    .tu-descriptions-item__container .tu-descriptions-item__content {
      display: inline-flex;
      align-items: baseline; }
    .tu-descriptions-item__container .tu-descriptions-item__content {
      flex: 1; }
  .tu-descriptions-item__label {
    color: #9baacf; }
    .tu-descriptions-item__label.has-colon::after {
      content: ":";
      position: relative;
      top: -0.5px; }
    .tu-descriptions-item__label.is-bordered-label {
      font-weight: bold; }
    .tu-descriptions-item__label:not(.is-bordered-label) {
      margin-right: 10px; }
  .tu-descriptions-item__content {
    word-break: break-word;
    overflow-wrap: break-word; }
